import React, { useState, useEffect, useRef, useCallback, Fragment, Suspense, lazy } from 'react';
import { useDispatch, useSelector } from "react-redux";
import { loginUser } from "../../redux/actions/authentication";
import { useHistory } from 'react-router-dom';
// import hook for translations and i18n configuration file
import { useTranslation } from "react-i18next";
import { useForm, Controller } from 'react-hook-form';
import ReCaptcha from "react-google-recaptcha";
import { Button } from 'primereact/button';
import { Checkbox } from 'primereact/checkbox';
import { InputText } from 'primereact/inputtext';
import { Toast } from "primereact/toast";

const v2CheckboxReCaptchaSiteKey = "6LeIxAcTAAAAAJcZVRqyHh71UMIEGNQ_MXjiZKhI";

const messageToast = (toast, severity, summary, detail) => {
    toast.current.show({ severity: severity, summary: summary, detail: detail, life: 3000 });
}

const LoginAccount = () => {

    const dispatch = useDispatch();
    const history = useHistory();
    // destructuring t() function for useTranslation()
    const [t, i18n] = useTranslation('common');
    const toast = useRef(null);
    const recaptchaLoginUserRef = useRef();

    /* functions to build form returned by useForm() hook */
    const {
        register,
        control,
        handleSubmit,
        reset,
        watch,
        setValue,
        getValues,
        // Read the formState before render to subscribe the form state through the Proxy
        formState: { errors, isDirty, isValid, isSubmitting, touchedFields, submitCount, dirtyFields } }
        = useForm();

    /* initialize state to hold */
    const [checked1, setChecked1] = useState(false);

    const error = (value) => {
        alert(value)
    }

    /* This is a special method for the implementation of this project */
    /* Currently method is fake login for user */
    const onSubmitLogin = (data, e) => {
        const recaptchaLoginUserResponse = recaptchaLoginUserRef.current.getValue();
        handleLogin(recaptchaLoginUserResponse, data);
    };

    const handleLogin = (recaptchaLoginUserResponse, data) => {
        const usernameOrEmail = data.usernameOrEmail;
        const password = data.password;
        dispatch(loginUser(recaptchaLoginUserResponse, usernameOrEmail, password))
            .then((response) => {
                if (response.data.length !== 0) {
                    history.push({
                        pathname: '/home'
                    });
                } else {
                    messageToast(toast, 'error', `${t("globalErrorMsg.errorMessageTitle1")}`, `${t("loginPage.error404MessageSubtitle")}`);
                    recaptchaLoginUserRef.current.reset();
                    reset({ usernameOrEmail: "", password: "", captchaLoginUser: null });
                }
            })
            .catch(() => {
                console.log(
                    'No internet connection found. App is running in offline mode.'
                );
            });
    };

    return (
        <Fragment>
            <Toast ref={toast} />
            <div className="login-body-custom">
                <div className="card login-panel p-fluid">
                    <div className="login-panel-content">
                        <form onSubmit={handleSubmit(onSubmitLogin)}>
                            <div className="p-grid">
                                <div className="p-col-12 p-sm-6 p-md-6 logo-container">
                                    <img src="assets/layout/images/logo-roma.svg" alt="roma" />
                                    <span className="guest-sign-in">{t('loginPage.formTitle', { framework: 'Roma' })}</span>
                                </div>
                                <div className="p-col-12 username-container">
                                    <div className="p-field">
                                        <label htmlFor="usernameOrEmail" className="p-d-block"><h5>{t('loginPage.usernameOrEmailTitle1')}</h5></label>
                                    </div>
                                    <div className="login-input">
                                        <Controller
                                            name="usernameOrEmail"
                                            control={control}
                                            defaultValue={"UsertestA"}
                                            rules={{
                                                required: true
                                            }}
                                            render={({ field: { onChange, value } }) =>
                                                <InputText type="text"
                                                    className={`p-inputtext p-widget p-state-default p-corner-all ${errors.usernameOrEmail ? 'p-invalid' : ''}`}
                                                    onChange={onChange} value={value} maxLength={35} placeholder={t('loginPage.usernameOrEmailSubtitle1')} />
                                            }
                                        />
                                    </div>
                                    {/* use to announce the error message */}
                                    {errors.usernameOrEmail && errors.usernameOrEmail.type === "required" && (
                                        <small id="usernameOrEmail1-help" className="p-error p-d-block">{t("globalInputErrorMsg.usernameOrEmailTitle1")}</small>
                                    )}
                                </div>
                                <div className="p-col-12 password-container">
                                    <div className="p-field">
                                        <label htmlFor="passwordTitle1" className="p-d-block"><h5>{t('globalInput.passwordTitle1')}</h5></label>
                                    </div>
                                    <div className="login-input">
                                        <div className="p-inputgroup">
                                            <Controller
                                                name="password"
                                                control={control}
                                                defaultValue={"Test0001"}
                                                rules={{
                                                    required: true,
                                                    pattern: /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])\S*$/i
                                                }
                                                }
                                                render={({ field: { onChange, value } }) => (
                                                    <InputText type={checked1 ? "text" : "password"}
                                                        className={`p-inputtext p-widget p-state-default p-corner-all ${errors.password ? 'p-invalid' : ''}`}
                                                        onChange={onChange} value={value} maxLength={15} placeholder={t('globalInput.passwordSubtitle1')} />
                                                )}
                                            />
                                            <span className="p-inputgroup-addon">
                                                <i className={checked1 ? "fa fa-fw fa-eye" : "fa fa-fw fa-eye-slash"}></i>
                                            </span>
                                            <span className="p-inputgroup-addon">
                                                <Checkbox checked={checked1} onChange={(e) => setChecked1(currentChecked1 => !currentChecked1)} />
                                            </span>
                                        </div>
                                    </div>
                                    {/* use to announce the error message */}
                                    {errors.password && errors.password.type === "required" && (
                                        <small id="password1-help" className="p-error p-d-block">{t("globalInputErrorMsg.passwordTitle1")}</small>
                                    )}
                                    {errors.password && errors.password.type === "pattern" && (
                                        <small id="password2-help" className="p-error p-d-block">{t("globalInputErrorMsg.passwordTitle2")}</small>
                                    )}
                                </div>
                                <div className="p-col-12">
                                    <Controller
                                        name="captchaLoginUser"
                                        control={control}
                                        defaultValue={""}
                                        rules={{ required: true }}
                                        render={({ field: { onChange } }) => (
                                            <ReCaptcha
                                                id="captchaLoginUser"
                                                ref={recaptchaLoginUserRef}
                                                sitekey={v2CheckboxReCaptchaSiteKey}
                                                onErrored={error}
                                                onChange={onChange}
                                            />)}
                                    />
                                    {/* use to announce the error message */}
                                    {errors.captchaLoginUser && errors.captchaLoginUser.type === "required" && (
                                        <small id="captchaLoginUser-help" className="p-error p-d-block">{t("globalInputErrorMsg.captchaTitle1")}</small>
                                    )}
                                </div>
                                <div className="p-col-12 p-sm-6 p-md-6 btnlogin-container">
                                    <Button type="submit" label={t('globalButton.formButtonTitle2')} icon="pi pi-check" />
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </Fragment>
    );
}

export default LoginAccount;